html,
body {
  height: 100%;
  overflow-x: hidden;
  font-family: 'Raleway', sans-serif; }

body {
  position: relative;
  display: flex;
  min-height: 100vh;
  flex-direction: column; }
  body.body--menu-open {
    overflow-y: hidden; }

main {
  flex: 1;
  margin-top: 4em; }

*,
*::before,
*::after {
  box-sizing: border-box; }

section {
  padding: 0.5em; }

.title {
  font-family: 'Righteous', cursive;
  text-transform: uppercase; }

.section-title {
  font-size: 2rem;
  background: -webkit-linear-gradient(#5ee7df, #b490ca);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; }

@font-face {
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 400;
  src: url("/static/fonts/raleway/raleway-v14-latin-regular.eot");
  /* IE9 Compat Modes */
