a {
  color: #000;
  font-weight: 600;
  text-transform: capitalize; }

#navbar__icon {
  display: inline-block;
  cursor: pointer;
  z-index: 9;
  margin: 1em; }

.bar1,
.bar2,
.bar3 {
  width: 35px;
  height: 5px;
  background-color: #333;
  margin: 6px 0;
  transition: 0.4s; }

.navbar__icon--open .bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px);
  transform: rotate(-45deg) translate(-9px, 6px); }

.navbar__icon--open .bar2 {
  opacity: 0; }

.navbar__icon--open .bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -8px);
  transform: rotate(45deg) translate(-8px, -8px); }

#navbar {
  width: 100%;
  height: 4em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  background: #fff; }

#navbar__brand {
  z-index: 9; }
  #navbar__brand > img {
    width: 12rem; }

#navbar__nav {
  position: absolute;
  height: 100vh;
  width: 100vw;
  background: #fff;
  top: 0;
  transform: translateX(100vw);
  transition: transform .5s ease-in-out;
  display: flex;
  flex-direction: column;
  justify-content: center; }
  #navbar__nav.navbar__nav--open {
    transform: translateX(0); }

.navbar__link {
  background: linear-gradient(190deg, #5ee7df, #b490ca);
  text-align: center;
  text-decoration: none;
  margin: 0.4em 0;
  height: 5em;
  display: flex;
  justify-content: center;
  align-items: center; }

.list__item {
  margin: 1em 0;
  padding: 0.5em;
  border-radius: 6px;
  background: linear-gradient(190deg, #5ee7df, #b490ca);
  box-shadow: 0 4px 6px 0 #bfbfbf; }

.list__title {
  margin: 0;
  color: #fff;
  text-shadow: 2px 2px 3px #000; }

.list__date {
  text-transform: capitalize; }

.footer__copyright {
  text-align: center;
  background: #d2d7d3;
  padding: 1em; }

#home_banner {
  min-height: 25em;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center; }
